import Vue from 'vue'
import Router from 'vue-router'

import AdminComponents from '../components/AdminComponents'
import RegisterComponent from '../components/RegisterComponent'
import LoginComponent from '../components/LoginComponent'

import Dashboard from '../components/Dashboard'
import Tutorials from '../components/Tutorials'
import Tutors from '../components/Tutors'
import Users from '../components/Users'
import Login from '../components/Login'
import Register from '../components/Register'
import ReviewTutorPage from '../components/ReviewTutorPage'

Vue.use(Router)

const loginRoutes = [
  { path: '', name: 'login', component: Login }
]

const registerRoutes = [
  { path: '', name: 'register', component: Register }
]

const adminRoutes = [
  { path: '', name: 'admin-dashboard', component: Dashboard },
  { path: 'tutorials', name: 'tutorials', component: Tutorials},
  { path: 'users', name: 'users', component: Users},
  { path: 'tutors', name: 'tutors', component: Tutors},
  { path: 'tutors/:user_id', name: 'review-tutor', component: ReviewTutorPage}
];

const router = new Router({
	routes: [
		{
			path: '/',
			component: AdminComponents,
			children: adminRoutes,
			meta: {
				requiresAuth: true,
			}
		},{
			path: '/admin-login',
			component: LoginComponent,
			children: loginRoutes,
			meta: {
				requiresVisitor: true,
			}
		},{
			path: '/admin-register',
			component: RegisterComponent,
			children: registerRoutes,
			meta: {
				requiresVisitor: true,
			}
		}
	]
})


export default router



